import { useEffect, useState } from "react";
import axious from "axios";
import {
  Menu,
  Container,
  KarakterGrubu,
  Sil,
} from "./styles/appcore";
import Modal from "./components/modal";

import { ThemeProvider } from "styled-components";
import { useDarkMode } from "./mood/useDarkMode";
import { ButonDegistir,BirinciButton,IkinciButton,LogOutButton,OlusturButonu } from "./mood/ButonDegistir";
import { GlobalStyles, aydinlikTema, karanlikTema } from "./mood/globalStyles";

import {
  Login,
} from "./styles/Login";
import setToken from "./services/login";

import Karakter from "./components/Karakter";

function App() {
  const [karakterData, setKarekterData] = useState([]);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState({
    message: [],
    css: "",
  });
  const [user, setUser] = useState(null);
  const [isKayitVisible, setIsKayitVisible] = useState(false);



  //--------------------------------------------------- Login İşlemi ve Localstoge'a Ekleme----------------------------------------------

  const handleLogin = async (event) => {
    event.preventDefault();

    try {
      const response = await axious.post("/api/login", { username, password });
      console.log(response)
      window.localStorage.setItem(
        "loggedFateappUser",
        JSON.stringify(response.data)
      );
        
      setUser(response.data);
      setUsername("");
      setPassword("");
    } catch (err) {
      setErrorMessage({ message: [err.response.data.error], css: "error" });

      setTimeout(() => {
        setErrorMessage({ css: "none", message: [""] });
      }, 5000);
    }
  };

  //--------------------------------------------------- Kullanıcı Ekleme----------------------------------------------
  const kullaniciEkle = async (e) => {
    e.preventDefault();
    try {
      await axious.post("/api/userlar", { username, password });
      setErrorMessage({ message: ["Kayıt Başarılı"], css: "ok" });
      setUsername("");
      setPassword("");
      setTimeout(() => {
        setErrorMessage({ css: "none", message: [""] });
      }, 5000);
    } catch (err) {
      setErrorMessage({ message: err.response.data.error, css: "error" });

      setTimeout(() => {
        setErrorMessage({ css: "none", message: [""] });
      }, 5000);
    }
  };

  //------------------------------------------------------------Kayıt Sayfası-------------------------------------------
  const kullaniciKayit = () => {
    return (
      <Login className="kagitBorder" onSubmit={kullaniciEkle}>
        <h1>Kayıt Ol</h1>
        <Modal {...errorMessage} />

        <div>
          <h3>Grup Adı</h3>
          <input
            type="text"
            value={username}
            name="Username"
            onChange={({ target }) => setUsername(target.value)}
          />
        </div>
        <div>
          <IkinciButton onClick={(e) => setIsKayitVisible(false)}>
            Giriş Yap
          </IkinciButton>
          <h3>Şifre</h3>
          <input
            type="password"
            value={password}
            name="Password"
            onChange={({ target }) => setPassword(target.value)}
          />
        </div>
        <BirinciButton type="submit">Kayıt Ol</BirinciButton>
      </Login>
    );
  };
  //------------------------------------------------------------Login Sayfası-------------------------------------------
  const loginForm = () => (
    <Login className="kagitBorder" onSubmit={handleLogin}>
      <h1>Giriş</h1>
      <Modal {...errorMessage} />
      <div>
        <h3>Grup Adı</h3>
        <input
          type="text"
          value={username}
          name="Username"
          onChange={({ target }) => setUsername(target.value)}
        />
      </div>
      <div>
        <h3>Şifre</h3>
        <IkinciButton onClick={(e) => setIsKayitVisible(true)}>
          Kayıt Ol
        </IkinciButton>
        <input
          type="password"
          value={password}
          name="Password"
          onChange={({ target }) => setPassword(target.value)}
        />
      </div>
      <BirinciButton type="submit">Giriş Yap</BirinciButton>
    </Login>
  );

  //------------------------------------------------------------Localstorage'daki Datayı React'a Aktarma-------------------------------------------
  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem("loggedFateappUser");
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      setUser(user);
    
    }
  }, []);

  //------------------------------------------------------- Logout İşlemleri ve Localstorage Silme --------------------------------------------------------
  const logout = (e) => {
    e.preventDefault();
    window.localStorage.removeItem("loggedFateappUser");

    setUser(null);
  };

  //-------------------------------------------------------Localstorage'daki Data ile Backend'den Veri Alma --------------------------------------------------------
  useEffect(() => {
    axious.get("/api/oyuncular").then((res) => {
      
      const userLocalStore = window.localStorage.getItem("loggedFateappUser");
      if (userLocalStore) {
        const parsedUser = JSON.parse(userLocalStore);
     
        
        const goster = res.data.filter((x) => x.user?.username === parsedUser.user);
        setKarekterData(goster);
      }
    });
  }, [user]);

  //------------------------------------------------------------Karakter Sayfası-------------------------------------------
  const karakterFormlari = () => (
    <KarakterGrubu>
      {karakterData.map((karakter) => {
        return (
          <Karakter
            key={karakter._id}
            formDelete={(e) => formDelete(e, karakter._id)}
            karakterData={karakter}
            guncelle={guncelle}
          />
        );
      })}
    </KarakterGrubu>
  );

  //------------------------------------------------------- DELETE --------------------------------------------------------
  const formDelete = async (e, id) => {
    e.preventDefault();
    try {
      const config = setToken(user.token);
      await axious.delete(`/api/oyuncular/${id}`, config);
      const newData = karakterData.filter((karakter) => karakter._id !== id);
      setKarekterData(newData);
    } catch (err) {
      alert(err.response.data.error);
    }
  };
  const hesapSil = (e) => {
    e.preventDefault();
    const config = setToken(user.token);
    if (window.confirm("Silmek istediğine emin misin?")) {
      axious.delete(`/api/userlar`,config).then((res) => {
        window.localStorage.removeItem("loggedFateappUser");

        setUser(null);
      });
    }
  };

  //------------------------------------------------------- POST --------------------------------------------------------
  const formEkle = (e) => {
    e.preventDefault();
    const karakterim = {
      karakterAdi: "",
      highConsept: "",
      trouble: "",
      aspect1: "",
      aspect2: "",
      aspect3: "",
      super: [""],
      harika: ["", ""],
      iyi: ["", "", ""],
      eh: ["", "", "", ""],
      duz: ["", "", "", "", ""],
      can: [false, false, false, false, false, false],
      mental: [false, false, false, false, false, false],
      stunts: "",
      sonuclar: [
        {
          no: 2,
          yazi: "",
        },
        {
          no: 4,
          yazi: "",
        },
        {
          no: 6,
          yazi: "",
        },
      ],
    };
    const config = setToken(user.token);
    console.log(config);
    axious.post(`/api/oyuncular`, karakterim, config).then((newperson) => {
      setKarekterData((prev) => {
        return [...prev, newperson.data];
      });
    });
  };

  //------------------------------------------------------- PUT --------------------------------------------------------
  const guncelle = (karakter, id) => {
    const config = setToken(user.token);
    return axious.put(`/api/oyuncular/${id}`, karakter, config);
  };

  //---------------------------------------------------- Karanlık Mod ------------------------------------------------
  const [tema, temaDegistir, temaEklendi] = useDarkMode();
  const temaModu = tema === "light" ? karanlikTema : aydinlikTema;
  if (!temaEklendi) return <div />;

  return (
    <ThemeProvider theme={temaModu}>
      <Container>
        <GlobalStyles />
        {user === null ? (
          <ButonDegistir tema={tema} temaDegistir={temaDegistir} />
        ) : (
          <Menu>
            <div>
              <h3>{user.user}</h3>
              <Sil onClick={hesapSil}>Hesabı Sil</Sil>
            </div>
            {<ButonDegistir tema={tema} temaDegistir={temaDegistir} />}{" "}
            {<LogOutButton onClick={logout}>Log out</LogOutButton>}{" "}
          </Menu>
        )}

        {user === null ? (
          <div>
            <div style={{ display: isKayitVisible ? "none" : "block" }}>
              {loginForm()}
            </div>
            <div style={{ display: isKayitVisible ? "block" : "none" }}>
              {kullaniciKayit()}
            </div>
          </div>
        ) : (
          karakterFormlari()
        )}

        {user && (
          <OlusturButonu onClick={formEkle}>
            <h3>+</h3>
          </OlusturButonu>
        )}
      </Container>
    </ThemeProvider>
  );
}

export default App;
