import styled from "styled-components";
export const DeleteButton = styled.button`
  border: none;
  position: absolute;
  left: 0;
  text-align: center;
  border-radius: 4px 0 0 0;
  border: solid 2px black;
  border-radius: 50%;
  font-weight: 600;
  height: 30px;
  width: 30px;
  color: #fff;
  background: #380000;
  z-index: 1;
  transition: all 0.3s ease;

  &:hover {
    background: chocolate;
  }
`;
